.home-main {
  display: flex;
  flex-direction: column;
  width: 100%;
  &-firts {
    display: flex;
    position: relative;
    overflow: hidden;
    height: 100vh;
    .back {
      &-color {
        position: absolute;
        z-index: 2;
        width: 100%;
        height: 100vh;
        background-color: rgba(34, 41, 44, 0.292);
      }
      &-container {
        position: absolute;
        width: 100%;
        height: 100vh;
        &-img {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100vh;
          background-position: center;
          background-size: cover;
          filter: blur(2px);
        }
      }
    }
    &-content{
      @include flex-center;
      z-index: 3;
      width: 100%;
      img{
        width: 200px; 
      }

      .text{
        color: $white;
        margin-left: 16px;
        width: 600px;
        p{
          font-size: 20px;
          font-weight: 500;
        }
      }

      @media (max-width: 450px) {
        flex-direction: column;
        z-index: 4;
        img{
          width: 100px;
        }

        .text{
          color: $white;
          margin-left: 0;
          margin-top: 16px;
          width: 310px;
          text-align: center  ;
          p{
            margin-top: 10px;
            font-size: 14px;
            font-weight: 500;
          }
        }
      }
    }

    .arrow-icon{
      position: absolute;
      z-index: 3;
      left: 50%;
      top: 90%;
      animation-name: iconMove;
      animation-duration: 1.2s;
      animation-iteration-count: infinite;
      animation-delay: 5s;
    }
  }
}


@keyframes iconMove {
  0% {top: 90%;}
  50% {top: 87%;}
  100% {top: 90%;}
}